import { Injectable } from '@angular/core';

import { Actions, Effect, ofType } from '@ngrx/effects';

import { Observable, of, from, empty } from 'rxjs';
import {
  catchError,
  exhaustMap,
  map,
  mergeMap,
  groupBy,
  flatMap,
  reduce,
  toArray,
} from 'rxjs/operators';

import {
  TransactionsActionTypes,
  Load,
  LoadSuccess,
  LoadFailure,
} from '../actions/transactions.actions';
import { TransactionService } from '../services/transaction.service';

@Injectable()
export class TransactionEffects {
  constructor(
    private actions: Actions,
    private transactionService: TransactionService,
  ) { }

  @Effect()
  load$: Observable<any> = this.actions.pipe(
    ofType<Load>(TransactionsActionTypes.Load),
    map((action) => action.payload),
    exhaustMap((payload: number) =>
      this.transactionService.getTransactions(payload).pipe(
        mergeMap((data) => {
          if (!data) {
            return empty();
          }
          return from(data.transactions).pipe(
            groupBy((transaction) =>
              new Date(transaction.date).setHours(0, 0, 0, 0),
            ),
            flatMap((group$) =>
              group$.pipe(
                reduce((acc, curr) => [...acc, curr], ['' + group$.key]),
                map((arr) => ({ date: arr[0], values: arr.slice(1) })),
              ),
            ),
            toArray(),
            map((newData) => {
              return new LoadSuccess({
                transactions: newData,
                total: data.total,
                currentPage: payload,
              });
            }),
            catchError((error) => of(new LoadFailure(error))),
          );
        }),
      ),
    ),
  );
}
