import * as auth from '../../auth/reducers/auth.reducer';
import * as transactions from '../../transactions/reducers/transactions.reducer';

import {
  ActionReducerMap,
  createFeatureSelector,
  createSelector,
  MetaReducer,
  ActionReducer,
} from '@ngrx/store';
import { localStorageSync } from 'ngrx-store-localstorage';
import { RouterStateUrl } from '../utils';

import * as fromRouter from '@ngrx/router-store';

export interface AppState {
  auth: auth.State;
  transactions: transactions.State;
  router: fromRouter.RouterReducerState<RouterStateUrl>;
}

export const reducers: ActionReducerMap<AppState> = {
  auth: auth.reducer,
  transactions: transactions.reducer,
  router: fromRouter.routerReducer,
};

export const localStorageSyncReducer = (
  reducer: ActionReducer<any>,
): ActionReducer<any> => {
  return localStorageSync({
    keys: [{ auth: ['isAuthenticated', 'merchant'] }],
    rehydrate: true,
  })(reducer);
};

export const logger = (
  reducer: ActionReducer<AppState>,
): ActionReducer<AppState> => {
  return (state: AppState, action: any): AppState => {
    console.log('current state', state);
    console.log('action', action);

    return reducer(state, action);
  };
};

export const metaReducers: Array<MetaReducer<any, any>> = [
  localStorageSyncReducer,
  logger,
];

// Auth selectors
export const AuthStateSelector = createFeatureSelector<auth.State>('auth');
export const getLoginStatus = createSelector(
  AuthStateSelector,
  auth.getLoginStatus,
);
export const getMerchant = createSelector(AuthStateSelector, auth.getMerchant);
export const getErrorMessage = createSelector(
  AuthStateSelector,
  auth.getErrorMessage,
);
export const getPendingStatus = createSelector(
  AuthStateSelector,
  auth.getPendingStatus,
);

// Transactions selectors
export const TransactionStateSelector = createFeatureSelector<
  transactions.State
>('transactions');
export const getCurrentTransactionPage = createSelector(
  TransactionStateSelector,
  transactions.getCurrentTransactionPage,
);
export const getTotalTransactions = createSelector(
  TransactionStateSelector,
  transactions.getTotalTransactions,
);
export const getTransactionPendingStatus = createSelector(
  TransactionStateSelector,
  transactions.getTransactionPendingStatus,
);
export const getTransactionErrorMessage = createSelector(
  TransactionStateSelector,
  transactions.getTransactionErrorMessage,
);
export const getTransactions = createSelector(
  TransactionStateSelector,
  transactions.getTransactions,
);
