import { Component } from '@angular/core';

import { Store, select } from '@ngrx/store';

import { Observable } from 'rxjs';

import { AuthComponent } from '../../components/auth/auth.component';

import { Merchant } from '../../../core/models/merchant';
import * as fromCore from '../../../core/reducers';
import * as AuthActions from '../../actions/auth.actions';

@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.scss'],
})
export class LoginPageComponent {

  error$: Observable<string | null> = this.store.pipe(select(fromCore.getErrorMessage));
  pending$: Observable<boolean> = this.store.pipe(select(fromCore.getPendingStatus));

  constructor(public store: Store<fromCore.AppState>) { }

  onSubmit($event: Merchant) {
    this.store.dispatch(new AuthActions.Login($event));
  }
}
