import { Action } from '@ngrx/store';

export enum TransactionsActionTypes {
  Load = '[Transaction] Load',
  LoadSuccess = '[Transaction] Load Success',
  LoadFailure = '[Transaction] LoadFailure',
}

export class Load implements Action {
  readonly type = TransactionsActionTypes.Load;

  constructor(public payload: any) { }
}

export class LoadSuccess implements Action {
  readonly type = TransactionsActionTypes.LoadSuccess;

  constructor(public payload: any) { }
}

export class LoadFailure implements Action {
  readonly type = TransactionsActionTypes.LoadFailure;

  constructor(public payload: any) { }
}

export type TransactionsUnion = Load | LoadSuccess | LoadFailure;
