import { Component, Input } from '@angular/core';
import {
  trigger,
  state,
  style,
  animate,
  transition,
} from '@angular/animations';

import { Transaction } from '../../../core/models/transaction';

@Component({
  selector: 'app-transaction',
  templateUrl: './transaction.component.html',
  styleUrls: ['./transaction.component.scss'],
  animations: [
    trigger('toggle', [
      state(
        'inactive',
        style({
          height: '0',
          opacity: '0',
          pointerEvents: 'none',
        }),
      ),
      state(
        'active',
        style({
          height: '*',
          opacity: '1',
          pointerEvents: 'auto',
        }),
      ),
      transition('inactive => active', animate('0.2s ease-in')),
      transition('active => inactive', animate('0.2s ease-out')),
    ]),
  ],
})
export class TransactionComponent {
  @Input() transaction: Transaction;
  @Input() isLast: boolean;
  @Input() isFirst: boolean;
  toggle: string = 'inactive';

  constructor() { }

  appliedClass(transaction: Transaction): void {
    console.log(transaction);
  }

  public toggleState(): void {
    this.toggle = this.toggle === 'active' ? 'inactive' : 'active';
  }
}
