import { TransactionGroup } from '../../core/models/transaction';
import {
  TransactionsActionTypes,
  TransactionsUnion,
} from '../actions/transactions.actions';

export interface State {
  total: number;
  transactions: null | TransactionGroup[];
  errorMessage: null | string;
  pending: boolean;
  currentPage: number;
}

export const initialState: State = {
  total: 0,
  transactions: null,
  errorMessage: null,
  pending: false,
  currentPage: 1,
};

export const reducer = (state = initialState, action: TransactionsUnion) => {
  switch (action.type) {
    case TransactionsActionTypes.Load: {
      return {
        ...state,
        errorMessage: null,
        pending: true,
      };
    }
    case TransactionsActionTypes.LoadSuccess: {
      return {
        ...state,
        pending: false,
        total: action.payload.total,
        transactions: action.payload.transactions,
        currentPage: action.payload.currentPage,
      };
    }
    case TransactionsActionTypes.LoadFailure: {
      return {
        ...state,
        pending: false,
        errorMessage: action.payload.error.text,
      };
    }
    default: {
      return state;
    }
  }
};

export const getCurrentTransactionPage = (state: State) => state.currentPage;
export const getTotalTransactions = (state: State) => state.total;
export const getTransactionPendingStatus = (state: State) => state.pending;
export const getTransactionErrorMessage = (state: State) => state.errorMessage;
export const getTransactions = (state: State) => state.transactions;
