import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';

import { NgxPaginationModule } from 'ngx-pagination';


import { StoreModule } from '@ngrx/store';
import {
  StoreRouterConnectingModule,
  RouterStateSerializer,
} from '@ngrx/router-store';
import { CustomRouterStateSerializer } from './core/utils';

import { EffectsModule } from '@ngrx/effects';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';

import { AppRoutingModule } from './app-routing.module';

import { AppComponent } from './app.component';

import { AuthComponent } from './auth/components/auth/auth.component';
import { LoginPageComponent } from './auth/containers/login-page/login-page.component';

import { TransactionListComponent } from './transactions/components/transaction-list/transaction-list.component';
import { TransactionComponent } from './transactions/components/transaction/transaction.component';

import { StatusComponent } from './status/components/status/status.component';
import { LoginStatusComponent } from './status/containers/login-status/login-status.component';

import { AuthService } from './auth/services/auth.service';
import { AuthGuard } from './auth/services/auth.guard';
import { LoginGuard } from './auth/services/login.guard';
import {
  TokenInterceptor,
  ErrorInterceptor,
} from './core/services/token.service';

import { AuthEffects } from './auth/effects/auth.effects';
import { TransactionEffects } from './transactions/effects/transactions.effects';
import { reducers, metaReducers } from './core/reducers';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';
import { AmountPipe } from './core/pipes/amount.pipe';
import { CustomerPipe } from './core/pipes/customer.pipe';
import { CardPipe } from './core/pipes/card.pipe';
import { ListComponent } from './transactions/containers/list/list.component';

@NgModule({
  declarations: [
    AppComponent,
    LoginPageComponent,
    TransactionListComponent,
    AuthComponent,
    StatusComponent,
    LoginStatusComponent,
    TransactionComponent,
    AmountPipe,
    CustomerPipe,
    CardPipe,
    ListComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    ReactiveFormsModule,
    HttpClientModule,
    InfiniteScrollModule,
    StoreModule.forRoot(reducers, { metaReducers }),
    StoreRouterConnectingModule.forRoot({
      stateKey: 'router',
    }),
    StoreDevtoolsModule.instrument({
      name: 'ArsenalPay',
    }),
    EffectsModule.forRoot([AuthEffects, TransactionEffects]),
    NgxPaginationModule
  ],
  providers: [
    AuthService,
    AuthGuard,
    LoginGuard,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true,
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ErrorInterceptor,
      multi: true,
    },
    { provide: RouterStateSerializer, useClass: CustomRouterStateSerializer },
  ],
  bootstrap: [AppComponent],
})
export class AppModule { }
