import { Component, Input } from '@angular/core';

import { Store, select } from '@ngrx/store';

import { Observable } from 'rxjs';

import * as fromAuth from '../../../core/reducers';
import * as AuthActions from '../../../auth/actions/auth.actions';
import { Merchant } from '../../../core/models/merchant';

@Component({
  selector: 'app-status',
  templateUrl: './status.component.html',
  styleUrls: ['./status.component.scss'],
})
export class StatusComponent {
  @Input() isAuthenticated: Observable<boolean>;
  @Input() merchant: Observable<Merchant>;

  constructor(private store: Store<fromAuth.AppState>) {}

  logout(): void {
    this.store.dispatch(new AuthActions.Logout());
  }
}
