import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

import { Observable } from 'rxjs';

import { Transaction } from '../../core/models/transaction';

interface TransactionModel {
  transactions: Transaction[];
  total: number;
}

@Injectable({
  providedIn: 'root',
})
export class TransactionService {
  private API_BASE = 'http://localhost:1337/transactions';

  constructor(private http: HttpClient) { }

  getTransactions(page: number): Observable<TransactionModel> {
    const params = new HttpParams()
      .set('page', `${page}`);
    return this.http.get<TransactionModel>(this.API_BASE, { params });
  }

  updateTransactionStatus(transaction: Transaction): Observable<Transaction> {
    return this.http.put<Transaction>(
      `${this.API_BASE}/${transaction._id}`,
      transaction,
    );
  }
}
