import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'amount',
})
export class AmountPipe implements PipeTransform {
  transform(
    value: any,
    status: string,
    decimals: number = 2,
    dec: string = '.',
    thousandsSeparator: string = ' ',
  ): any {
    const check: any = !isFinite(value) ? 0 : value;
    const prec: number = !isFinite(decimals) ? 0 : Math.abs(decimals);

    const toFixed = (check, prec): number => {
      const k: number = Math.pow(10, prec);
      return Math.round(check * k) / k;
    };

    const splitted: Array<string> = (prec
      ? toFixed(check, prec)
      : Math.round(check)
    )
      .toString()
      .split('.');

    if (splitted[0].length > 3) {
      splitted[0] = splitted[0].replace(
        /\B(?=(?:\d{3})+(?!\d))/g,
        thousandsSeparator,
      );
    }

    if ((splitted[1] || '').length < prec) {
      splitted[1] = splitted[1] || '';
    }

    const result = `${status && status === 'refund' ? '- ' : ''}${splitted.join(splitted[1] ? dec : '')} â‚½`

    return result;
  }
}
