import { Component, OnInit } from '@angular/core';
import { Store, select } from '@ngrx/store';

import { Observable } from 'rxjs';

import { TransactionGroup } from '../../../core/models/transaction';

import * as fromCore from '../../../core/reducers';
import * as TransactionActions from '../../actions/transactions.actions';
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
})
export class ListComponent implements OnInit {
  transactions$: Observable<TransactionGroup[]> = this.store.pipe(
    select(fromCore.getTransactions),
  );
  total$: Observable<number> = this.store.pipe(
    select(fromCore.getTotalTransactions),
  );
  currentPage$: Observable<number> = this.store.pipe(
    select(fromCore.getCurrentTransactionPage),
  );
  pending$: Observable<boolean> = this.store.pipe(
    select(fromCore.getTransactionPendingStatus),
  );
  errorMessage$: Observable<null | string> = this.store.pipe(
    select(fromCore.getTransactionErrorMessage),
  );

  constructor(public store: Store<fromCore.AppState>) {}

  ngOnInit(): void {
    this.getTransactions(1);
  }

  getTransactions($event: number): void {
    this.store.dispatch(new TransactionActions.Load($event));
  }
}
