import { Merchant } from '../../core/models/merchant';
import { AuthUnion, AuthActionTypes } from '../actions/auth.actions';

export interface State {
  isAuthenticated: boolean;
  merchant: Merchant | null;
  errorMessage: string | null;
  pending: boolean;
}

export const initialState: State = {
  isAuthenticated: false,
  merchant: null,
  errorMessage: null,
  pending: false,
};

export const reducer = (state = initialState, action: AuthUnion): State => {
  switch (action.type) {
    case AuthActionTypes.Login: {
      return {
        ...state,
        errorMessage: null,
        pending: true,
      };
    }
    case AuthActionTypes.LoginSuccess: {
      return {
        ...state,
        isAuthenticated: true,
        pending: false,
        merchant: {
          login: action.payload.login,
        },
      };
    }
    case AuthActionTypes.LoginFailure: {
      return {
        ...state,
        pending: false,
        errorMessage: action.payload.error.text,
      };
    }
    case AuthActionTypes.Logout: {
      return initialState;
    }
    default: {
      return state;
    }
  }
};

export const getLoginStatus = (state: State) => state.isAuthenticated;
export const getMerchant = (state: State) => state.merchant;
export const getErrorMessage = (state: State) => state.errorMessage;
export const getPendingStatus = (state: State) => state.pending;
