import { Pipe, PipeTransform } from '@angular/core';

import { CARD_TYPES, CARD_LIST } from '../CONSTANTS';
import { CardList } from '../models/card';

@Pipe({
  name: 'card'
})
export class CardPipe implements PipeTransform {

  transform(cardNumber: any): any {
    const unknownCard: CardList = CARD_LIST.filter(
      (system) => system.type === CARD_TYPES.UNKNOWN_CARD,
    )[0];

    let paymentSystem: CardList | null = null;

    CARD_LIST.forEach((card) => {
      card.patterns.forEach((pattern) => {
        const number: string = String(cardNumber);
        const strPattern: string = String(pattern);

        if (number.substring(0, strPattern.length) === strPattern) {
          paymentSystem = card;
        }

      });
    });

    return paymentSystem !== null ? paymentSystem.type : unknownCard.type;
  }
}
